import MUIDataTable from "mui-datatables";
import React, { useState, useEffect } from "react";
import Button from "@material-ui/core/Button";
import { columns, errorCols, errorOptions } from "./data_header_table";
import { createMuiTheme, MuiThemeProvider } from "@material-ui/core/styles";
import { Formik, Form, ErrorMessage } from 'formik'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import * as Yup from 'yup'
import Swal from 'sweetalert2'
import { Grid } from '@material-ui/core';
import { Fab } from "@material-ui/core";
import { requesicoes } from "../../../utils/services";
import Input from '../../../ui-component/shared/Input';


let paises = [];
export default function MuiDatatableExample() {
  const getMuiTheme = () =>
    createMuiTheme({
      overrides: {
        MUIDataTableHeadCell: {
          fixedHeader: { backgroundColor: "darkblue", color: "white" }
        },
        MUIDataTableSelectCell: {
          headerCell: { backgroundColor: "darkblue" },
          checkboxRoot: { color: "white", filter: "brightness(0.5)" },
          expandDisabled: {
            // Soft hide the button.
            visibility: "hidden"
          }
        }
      }
    });

  const [, setIsFormValid] = useState(false);
  //const [isLoading, setIsLoading] = useState(true);
  const [openPais, setOpenPais] = React.useState(false);
  const [updateDom, setUpdateDom] = useState(false);

  useEffect(() => {

    init();
  }, [])

  async function init() {

    const countries = await Promise.resolve(requesicoes().buscarPaises());
    paises=countries.data.map((x, i) => ({  ...x, NO: i + 1}))
       
    setUpdateDom(!updateDom);
  }

  const handleClickOpenPais = () => {
    setOpenPais(true);
  };

  const handleClose = () => {
    setOpenPais(false);

  };

  const options = {
    // filterType: "checkbox",
    responsive: "standard",
    elevation: 10,
    print: true,
    selectToolbarPlacement: "true",
    selectableRowsOnClick: true,
    expandableRows: false,
    expandableRowsHeader: false,
    isRowExpandable: (dataIndex, ) => {
      const { errors } = paises[dataIndex];
      return errors !== null && errors !== undefined;
    },
    onRowExpansionChange: (curExpanded, allExpanded, rowsExpanded) =>
      console.log(curExpanded, allExpanded, rowsExpanded),
    renderExpandableRow: (rowData, rowMeta) => {

      return (
        <div
          style={{
            marginLeft: "90%",
            width: "100%",
            marginTop: "3%",
            marginBottom: "3%"
          }}
        >
          <MUIDataTable
            columns={errorCols}
            paises={paises[5]}
            options={errorOptions}
          />
        </div>
      );
    },
    customToolbarSelect: (selectedRows, displayData, setSelectedRows) => {
      console.log(selectedRows, displayData);
      return <div> am youre toolbar header </div>;
    },
    nextPath(path) {
      this.props.history.push(path);
    },
    customToolbar: () => {

      return (

        <Fab onClick={handleClickOpenPais} variant="extended" size="medium"
          style={{
            position: 'fixed',
            bottom: '60px', zIndex: 500,
            right: '15px'
          }} color="primary">Adicionar</Fab>


      );
    },
    onRowSelectionChange: (
      currentRowsSelected,
      allRowsSelected,
      rowsSelected
    ) => {
      if (
        allRowsSelected !== null &&
        allRowsSelected !== undefined &&
        allRowsSelected.length > 0
      ) {
        setIsFormValid(true);
      } else {
        setIsFormValid(false);
      }
      console.log(currentRowsSelected, allRowsSelected, rowsSelected);
      rowsSelected.map((item) => console.log(paises[item]));
    }
  };
  const validationSchema = Yup.object().shape({
    name: Yup.string().min(3, "Deve ter no minio 3 caratere").required("Campo Obrigatorio"),
  })
  return (
    <div>
      <MuiThemeProvider theme={getMuiTheme()}>
        <MUIDataTable
          title={'Listagem de Países'}
          columns={columns(paises)}
          data={paises}
          options={options}
        // components={components}
        />
      </MuiThemeProvider>


      {/* Dialogo Pais cadastro */}
      <Dialog open={openPais} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Cadastrar País</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Preencha os dados do país que pretende cadastrar
          </DialogContentText>
          <Formik initialValues={{}} validationSchema={validationSchema}

            onSubmit={(values, { setSubmitting }) => {
              async function save(values) {
                try {

                  await Promise.resolve(requesicoes().postPais([values]));
                  //setOpen(true)
                  Swal.fire(
                    'Exito!',
                    'Pais cadastrado com sucesso!',
                    'success'
                  )
                  init();

                }
                 catch (error) {
                  if(error.response.status ===403){
                    Swal.fire({
                      icon: 'warning',
                      title: 'Oops...',
                      text: 'Operacao nao permitida devido ao nivel de acesso',
                      footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                    })
                  }else{
                  console.log("Codigo: ",error.response.status);

                    console.log("Codigo: ",error.status);
                    Swal.fire({
                      icon: 'error',
                      title: 'Oops...',
                      text: 'Ocorreu um erro ao cadastrar Curso!!',
                      footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                    })
                  }

                 

                }
              }
              save(values);
              setTimeout(() => {
                setSubmitting(false);
              }, 500);
            }}>
            {(props) => {
              const { isSubmitting, setFieldValue, values } = props;
              return (
                <Form>
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={6}>
                      <Input name="name"
                        label="Nome"
                        placeholder="Nome do país"
                        helperText={<ErrorMessage name='name' />}
                        error={props.errors.name && props.touched.name} />

                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Input
                        fullWidth
                        label="Sigla"
                        name="short_name"
                        placeholder="Sigla do País"
                        helperText={<ErrorMessage name='short_name' />}
                        error={props.errors.short_name && props.touched.short_name} />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Input
                        fullWidth
                        label="Codigo"
                        name="country_code"
                        placeholder="Codigo do País"
                        helperText={<ErrorMessage name='country_code' />}
                        error={props.errors.country_code && props.touched.country_code} />
                    </Grid>

                  </Grid>
                  <DialogActions>
                    <Button onClick={handleClose}>Cancelar</Button>
                    <Button type="submit" onClick={handleClose}>Salvar</Button>
                  </DialogActions>
                </Form>)
            }}
          </Formik>
        </DialogContent>


      </Dialog>

    </div>

  );
}
